.component {
  margin-bottom: 128px; }
  .component h2 {
    width: 100%;
    background-color: #999;
    color: white;
    padding: 0 16px;
    margin: 16px -16px; }
  .component button {
    font-family: 'Vectrex';
    color: white;
    background-color: grey;
    border: 1px solid black;
    margin-right: 4px; }
  .component .slider {
    margin: 64px 64px 100px 16px;
    font-size: 10px; }
    .component .slider .label {
      font-size: 12px;
      position: absolute;
      top: -32px;
      width: 100%; }
      .component .slider .label .value {
        position: absolute;
        right: 0;
        color: black; }
    .component .slider .noUi-value {
      font-size: 6px; }
    .component .slider .noUi-value-horizontal {
      margin-top: 12px; }
  .component.floater {
    position: fixed;
    right: 0;
    top: 0;
    border: 1px solid black;
    background-color: white;
    opacity: 0.8;
    box-shadow: -5px 6px 38px 0px rgba(0, 0, 0, 0.24);
    margin: 32px; }
    .component.floater h2 {
      margin: 0;
      padding: 0; }
    .component.floater .horizontal-meter {
      width: 30vw;
      height: 32px; }
      .component.floater .horizontal-meter .bar-empty {
        position: absolute;
        width: 100%;
        height: 32px;
        background-color: grey; }
      .component.floater .horizontal-meter .bar-fill {
        position: absolute;
        height: 100%;
        background-color: lightgreen; }
      .component.floater .horizontal-meter .label {
        position: absolute;
        top: 0;
        font-size: 16px;
        color: darkgrey; }
    .component.floater .banded-meter {
      width: 30vw;
      height: 128px; }
      .component.floater .banded-meter .drawing-area {
        position: absolute;
        width: 100%;
        height: 128px; }
      .component.floater .banded-meter .label {
        position: absolute;
        top: 0;
        font-size: 16px;
        color: darkgrey; }
      .component.floater .banded-meter canvas {
        position: absolute;
        width: 100%;
        height: 100%; }
